*{box-sizing: border-box;}

section{display: flex;flex-direction: column; width: 100%; height: 100vh;  }

button{ outline: none;border: 0; cursor: pointer;}

.navbar{font-size: 2rem;padding: 1em; background: blanchedalmond;}
.navbar-logo{color: green;margin-right: 0.5em;}
.navbar-count{display: inline-block; text-align: center; font-size: 2rem; width: 2.5rem;height: 2.5rem; line-height: 2.5rem; margin-right: 0.2em; background: green; color: #fff; border-radius: 50%;}

.add-input{font-size: 2rem; margin-right: 0.2em;}
.add-button{height: 100%;font-size: 1.5rem;padding: 0.3em 0.5em; background: green; color: #fff;}

.habit{font-size: 2.5rem; padding: 0.5em; list-style: none;}
.habit-count{display: inline-block; text-align: center; font-size: 2rem; width: 2.5rem; height: 2.5rem; line-height: 2.5rem; margin-left: 0.2em;margin-right: 0.5em;background: darkseagreen; color: #fff; border-radius: 50%;}

.habit-button{font-size: 2.5rem;margin: 0 0.1em; background: transparent;}
.habit-button:hover{opacity: 0.8;}
.habit-increase,
.habit-decrease{color: goldenrod;}
.habit-delete{font-size: 2rem;color: darkred;}

.habits-reset{font-size: 1.5rem; padding: 0.2em 1em; background: green; color: #fff;}







